* {
    font-family: 'Montserrat', sans-serif;
    margin: 0;
    padding: 0;
}

.container {
    display: flex;
    justify-content: center;
    width: 55%;
    background-color: white;
    margin-bottom: 50px;
}

@media (max-width: 770px) {
    .container {
        width: 100%;
    }
}
